.App {
  max-width: 800px;
  margin: 0 auto;
}

.App-nav{
  margin: 10px;
  padding: 20px;
  border-bottom: 2px solid rgb(19, 48, 112);
}

.App-nav ul {
  padding: 0;
  margin: 0;
}

.App-nav button {
  font-size: 18px;
  background: transparent;
  border: none;
  cursor: pointer;
}

.App-nav-item {
  list-style: none;
  display: inline-block;
  margin-right: 32px;
  font-size: 24px;
  border-bottom: 4px solid transparent;
}

.App-nav-item button:hover {
  color: #bcc0cf;
}

.App-nav-item.selected {
  border-bottom-color: #19be35 ;
}

.App-content {
  margin: 0 30px;
}
